<script>

    import {
         mapGetters
     } from 'vuex'
    import axios from 'axios'
    import {API} from '../../store/config.js'
    import { Datetime } from 'vue-datetime';
export default {
     components: {
        Datetime
        },
    computed:{
             ...mapGetters(["data"])
         },
    data(){
        return{
            table:'',
            activeSerach:"d-none",
            loading:false,
            body:[],
            data_id:[],
            isActive:'',
            CategoryIdChilds:'',
            Categoryid:'',
            Chailds:'',
            date:  new Date().toISOString(),
            pickerDate: null,
            selected: null,
            options: [
            { value: null, text: 'Please select an option' },
            { value: '1', text: 'Стол 2' },
            { value: '2', text: 'Стол 3' },
            ],
            item:0
        }
    },
    methods: {
      changedish(){
        const token = localStorage.getItem('D-Token')
        axios
            .get (API+'category/'+this.body[this.$route.params.id].slug+'/dishes',
                {
                  headers: {
                    Authorization: `Bearer ${token}`
                  }
                }
            )
            .then(res=>{
                  this.$store.dispatch("dishesmenu",res.data.data)
                }
            )
        console.log("hello")
      },
        countPlus(){
            this.item++
        },
        countMinus(){
            if(this.item>0){
                this.item--
            }
        },
        activeS(){
            if(this.activeSerach=="d-none"){
                this.activeSerach='d-flex'
            }
            else{
                this.activeSerach='d-none'
            }
        },
        ApiId(Categoryid){
            const token = localStorage.getItem('D-Token')
            axios.get(API+'category/'+Categoryid+'/'+'childs',
                {
                    headers: {
                    Authorization: `Bearer ${token}`
                  }

                })
                .then(res=>{
                    this.spinner=false
                    this.Chailds=res.data.data
                   this.$store.dispatch("data",res.data.data)
                })
        },
        postOrder(){

            const token = localStorage.getItem('D-Token')
            let dateTime=new Date(this.date)
            this.loading=true
            axios
            .post(API+'order/order-table',{
                "organization_id":1,
                "date":parseInt(dateTime.getTime()/1000),
                "people_count":this.item,
                "table_number":this.selected
            },
            {
            headers: {
                Authorization: `Bearer ${token}`
            }
            }
            )
            .then(()=>{
                this.$toasted.show("Succes", {
                   duration: 2000,
                });
             this.loading=false
            })
            .catch(() => {
                this.loading=false
            })



        }
    },
mounted() {
axios.get(API+"device/login-qr?hash=5|5|key")
.then(res=>{
  this.$router.push('/0')
  this.isActive=this.$route.params.id
localStorage.setItem('D-Token',res.data.data.token);
const token = localStorage.getItem('D-Token')
    axios
    .get(API+'organization/'+res.data.data.organization_id+'/tables',
        {
        headers: {
            Authorization: `Bearer ${token}`
        }
        }
        )
        .then(res=>{
            this.table=res.data.data
        })

    axios
    .get (API+'category/index',
    {
        headers: {
        Authorization: `Bearer ${token}`
    }

    })
    .then(res=>{
      this.$store.dispatch("data",res.data.data)
        this.body=res.data.data
      axios
          .get (API+'category/'+res.data.data[this.$route.params.id].slug+'/dishes',
              {
                headers: {
                  Authorization: `Bearer ${token}`
                }
              }
          )
          .then(res=>{
                this.$store.dispatch("dishesmenu",res.data.data)
              }
          )
    })


})



},

}
</script>
<style>
    .active-btn{
     color: #fff;
    background: linear-gradient(135deg,#f98445 3.88%,#f74873 96.12%);
    width: 100%;
    height: 50px;
    text-align: center;
    border-radius: 8px;
    font-weight: 600;
    font-size: 14px;
    }
   body{
       background-color: #f4f6fa !important;
   }
   a{
       text-decoration: none  !important;
   }
   button{
       border: 0 !important;
       outline: 0 !important;
   }
   @media(max-width:750px)
 {  .no-active{
    display:none
   }
   }
