       border: 0 !important;
   }
   .modal-header .close{
           color: #00000057;
   }
   .modal-body{
    padding: 15px 35px 30px 35px !important;
   }
   .modal-title{
    margin: 0 auto;
    margin-left: 46px;
   }
   .vdatetime-input{
    border: 1px solid   #f86e56;
    width: 100%;
    height: 50px;
    padding: 0 15px;
    border-radius: 8px;
    cursor: pointer;
   }
   .cursor-pointer{
       cursor: pointer;
   }
   .dropModal .dropdown{
    border-radius: 8px;
    margin-top: 12px;
    padding: 5px;
    width: 100%;
    background: red;

   }

   .dropModal select{
       height: 50px;
       font-size: 14px;
       border: 1px solid   #f86e56;
   }
   .modal-title{
       font-size: 16px !important;
   }
   .modal-dialog{
       margin: 6.75rem auto !important;
   }
   .count-items-tabl{
    width: 50px;
    border: 1px solid #e2e6ed;
    height: 50px;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

   }
   .count-items-tabl-input input{
    border: 1px solid #e2e6ed;
    height: 50px;
    width: 110px;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    padding: 0 auto;
    padding: 0 0 0 52px;
   }
   .justify-content-space-between{
       justify-content: space-between;
   }
   @media(max-width: 766px){
   .table-order{
    display: block !important;
    margin: 0px 0 20px auto;
   }
   }
