<template>
    <div class="index-page">
        <div class="container-fluid">
            <div class="row">
                <div class="section-1">
                    <div class="my-swiper">
                        <swiper ref="mySwiper" :options="swiperOptions" id="appartmentCarousel">
                            <swiper-slide class="swiper-slide" v-for="(item,index) in data[$route.params.id].children" :key="index">
                                <button @click="IsActive=('qwerty'+item.id);Funck(dishesslug=item.slug)"
                                    :class="{ 'active' : IsActive==('qwerty'+item.id) }"
                                    class="my-slide">{{item.name}}</button>
                            </swiper-slide>
                        </swiper>

                    </div>
                </div>
                <div class="section-2 mt-3">
                    <div class="row">
                        <button class="col-lg-3 col-md-4 col-sm-6 col-12  mar-bot" v-for="(item,index) in dishesmenu"
                            :key="index" :class="{'pointer-events': item.active==false}">
                            <div @click="IsDishes=('qwertyy'+item.id);toggleActive(index);FoodsList(list=dishesmenu,index);"
                            :class="{active: item.active,'pointer-events': item.active==false }" class="eat-card "
                                :id="'abs'+item.id">
                                <div class="first">
                                    <img :src="item.poster" alt="" class="eat-img">
                                    <div class="eat-name">{{item.title}}</div>
                                    <div class="eat-info"><span>{{item.price}}</span><span>
                                            <img src="../../assets/images/icons/clock.svg" alt=""> 15м</span>
                                    </div>
                                </div>
                                <div class="second" >
                                    <div class="title">Хотите узнать подробнее 0 salad 20</div>

                                    <router-link :to="'/about/'+item.id" class="more">Да мне интресно</router-link>
                                    <button  class="more">Не интресно</button>
                                </div>
                                <div class="checking">
                                    <img  :class="{'d-block': item.active==false}" src="../../assets/images/icons/check.svg" alt="">
                                </div>
                            </div>
                        </button>
                    </div>
                </div>

            </div>
        </div>
        <div class="loading" >
            <b-spinner v-if="dishesmenu.length==0" label="Loading..." variant="danger"></b-spinner>
        </div>
    </div>
</template>

<script>

    import {
        mapGetters
    } from 'vuex'
    import axios from 'axios'
    import { API } from '../../store/config.js'
    import { foods } from '../../store/foodslist.js'
    import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'

    export default {
        components: {
            Swiper,
            SwiperSlide
        },
        directives: {
            swiper: directive
        },
        data() {
            return {
                body1: [],
                item: 1,
                IsDishes: 'qwertyy',
                IsActive: 'qwerty'+7,
                body: [],
                dishes: [],
                swiperOptions: {
                    slidesPerView: 3.4,
                    spaceBetween: 20,
                    navigation: {
                        nextEl: ".swiper-button-next",
                        prevEl: ".swiper-button-prev",
                    },
                    breakpoints: {
                        0: {
                            slidesPerView: 1,
                        },
                        576: {
                            slidesPerView: 1.2,
                        },
                        768: {
                            slidesPerView: 3,
                        },
                        992: {
                            slidesPerView: 3,

                        },
                        1200: {
                            slidesPerView: 4,
                        }
                    }
                },
                loading: true

            }



        },
        methods: {
          changedish() {
            const token = localStorage.getItem('D-Token')
            axios
                .get(API + 'category/' + this.body[this.$route.params.id].slug + '/dishes',
                    {
                      headers: {
                        Authorization: `Bearer ${token}`
                      }
                    }
                )
                .then(res => {
                      this.$store.dispatch("dishesmenu", res.data.data)
                    }
                )
          },
            FoodsList(list,index) {
                console.log(list[index].active);
                if(list[index].active==true)
                {
                    foods.push({
                    food : list[index],
                    count : 1
                })
                }
                this.$store.dispatch("foodslist", foods)
            },
            Funck(dishesslug) {
              const token = localStorage.getItem('D-Token')
              axios
                  .get (API+'category/'+dishesslug+'/dishes',
                      {
                        headers: {
                          Authorization: `Bearer ${token}`
                        }
                      }
                  )
                  .then(res=>{
                        this.$store.dispatch("dishesmenu",res.data.data)
                      }
                  )
            },
            toggleActive(index) {
            let item = this.$store.state.dishesmenu[index];
            item.active = !item.active;
            this.$set(this.$store.state.dishesmenu, index, item);
            },
            minus() {
                if (this.item > 0) {
                    this.item--
                }
            },
            pilus() {
                this.item++
            },
        },
        computed: {
            ...mapGetters(["data", "dishesmenu"])

        },
        mounted() {
            // this.IsActive = this.IsActive + this.$store.state.data[0].id
        },

    }
</script>
<style>
    .pointer-events{
        pointer-events: none;
    }
    .index-page .container-fluid .section-1 .my-swiper .swiper-container .my-slide {
        text-decoration: none;
    }

    body {
        background-color: #f4f6fa !important;
    }

    .swiper-slide {

        width: 180px !important;
    }

    .my-slide.router-link-exact-active {
        background: linear-gradient(135deg, #f98445 3.88%, hsl(345, 92%, 63%) 96.12%) !important;
        color: #fff !important;
    }

    .loading {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .pointer-event{
        pointer-events: none;
    }
    .zakaz-page .container-fluid .section-2 .eat-card-2 .first{
        height: 100% !important;
    }
    .index-page .container-fluid .section-2 .eat-card .first .eat-img{
        height: 150px;
        object-fit: cover;
    }
</style>
