import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import error from '../views/Error.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/:id',
    component: {
      render(c) {
        return c('router-view')
      }
    },
    children: [{
      path: '/',
      component: Home
    },
      {
        path: '/about/:id',
        name: 'About',
        component: () => import( '../views/About.vue')
      },
      {
        path: 'order',
        name: 'order',
        component: () => import( '../views/order.vue')
      },
      {
        path: 'rating',
        name: 'rating',
        component: () => import( '../views/Rating.vue')
      },
      {
        path: 'chacklist',
        name: 'chacklict',
        component: () => import( '../views/Chacklist.vue')
      },
      {
        path: 'salat',
        name: 'salat',
        component: () => import( '../views/Salat.vue')
      },

    ],
  },

    {
      path: '*',
      component: error
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
