import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export const store = new Vuex.Store({
    state: {
        data:[],
        foodslist:[],
        dishesmenu:[],
        check:[],
        dishes:[]    
    },
    getters: {
        data: (state) => {
            return state.data
        },

        foodslist: (state) => {
            return state.foodslist
        },

        dishesmenu: (state) => {
            return state.dishesmenu
        },

        check: (state) => {
            return state.check
        },
        
        dishes: (state) => {
            return state.dishes
        },
        
        
       
    },
    actions: {
        data(context, data) {
            context.commit('data', data)
        },
        foodslist(context, foodslist) {
          context.commit('foodslist', foodslist)
      },
      dishesmenu(context, dishesmenu) {
        context.commit('dishesmenu', dishesmenu)
    },

    check(context, check) {
        context.commit('check', check)
    },
    
    dishes(context, dishes) {
        context.commit('dishes', dishes)
    },

    },
    mutations: {
        data(state, data) {
            state.data = data
        },

        foodslist(state, foodslist) {
          state.foodslist = foodslist
      },

      dishesmenu(state, dishesmenu) {
        state.dishesmenu = dishesmenu
    },

    check(state, check) {
        state.check = check
    },

    
    dishes(state, dishes) {
        state.dishes = dishes
    },
    },

})